import $ from 'jquery';

$(document).ready(function () {
  $(document).on('input change', ':input', function () {
    $(this).closest('.form-group').find('.error-text').remove();
  });

  $(document).on('forminvalid', 'form', function () {
    if ($('.form-control.is-invalid').length) {
      $('.form-control.is-invalid').first().focus();
    }
  });

  // Make label act like buttons
  // when only using keyboards.
  $(document).on('keypress', 'label', function (e) {
    switch (e.keyCode) {
      case 13: // enter
      case 32: // space
        $('#'+$(this).attr('for')).trigger('click');
        e.preventDefault();
        break;
    }
  })
  $(document).on('keypress', 'span[role=button]', function (e) {
    switch (e.keyCode) {
      case 13: // enter
      case 32: // space
        $(this).trigger('click');
        e.preventDefault();
        break;
    }
  })
});
