import $ from 'jquery';
import './avatar-upload.scss';

function readURL(input) {
  if (input.files && input.files[0]) {
    var reader = new FileReader();

    reader.onload = function(e) {
      $(input).closest('[data-avatar]').find('[data-avatar-preview]').css('background-image', 'url('+e.target.result +')');
      $(input).closest('[data-avatar]').find('[data-avatar-preview]').hide();
      $(input).closest('[data-avatar]').find('[data-avatar-preview]').fadeIn(650);
      $(input).closest('[data-avatar]').find('[data-avatar-cancel]').fadeIn(230);
    }

    reader.readAsDataURL(input.files[0]);
  }
}

$('[data-avatar-upload]').change(function() {
  readURL(this);
});

$(document).on('click', '[data-avatar-cancel]', function (e) {
  let $parent = $(this).closest('[data-avatar]');

  $parent.find('[data-avatar-preview]').css('background-image', 'none');
  $parent.find('[data-avatar-cancel]').fadeOut(230);
  $parent.find('[type=file]').val('');

  $(this).trigger('avatar:remove');
  e.preventDefault();
});
