import $ from 'jquery';
import './photo-upload.js';

$(document).ready(function () {
  $(document).on('change', '[data-photo-preview-target]', function () {
    let target = $(this).data('photo-preview-target');
    let value = $(this).val();
    let $target = $(target);

    $target.attr('src', value);
  });

  $(document).find('[data-photo-preview-remove]').on('click', function () {
    const $photo = $(this).parents('[data-photo]');
    const target = $photo.find('[data-photo-preview-target]').data('photo-preview-target');

    $(target).attr('src', '');
    $photo.find('[data-photo-preview-target]').val('').trigger('change');
  });
});
