import $ from 'jquery';
import 'bootstrap/js/dist/modal.js';

$(document).ready(function () {
  // On load, show the modal if certain
  // URL parameters are met.
  const url = window.location.href;
  if (url.indexOf('modal=true') != -1) {
    const target = getUrlParam('id');
    $('#' + target).modal('show');
    $('#' + target).find(':input:enabled:visible:first').focus();
  }

  // Push the url params on button click
  $(document).find('[data-toggle=modal]').on('click', function () {
    const id = $(this).attr('data-target')
    const newurl = window.location.protocol + '//' +
                   window.location.host +
                   window.location.pathname +
                   '?modal=true&id=' + id.replace('\#', '');
    window.history.pushState({ path: newurl }, document.title, newurl);
  });

  $(document).find('[data-dismiss=modal]').on('click', function () {
    const id = $(this).parents('.modal').attr('id')
    const newurl = window.location.protocol + '//' +
                   window.location.host +
                   window.location.pathname +
                   '?modal=false&id=' + id.replace('\#', '');
    window.history.pushState({ path: newurl }, document.title, newurl);
  });
});

function getUrlVars() {
  var vars = {};
  var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
    vars[key] = value;
  });
  return vars;
}

function getUrlParam(parameter, defaultvalue){
  var urlparameter = defaultvalue;
  if (window.location.href.indexOf(parameter) > -1) {
    urlparameter = getUrlVars()[parameter];
  }
  return urlparameter;
}
